export class Color {
    protected readonly r: number;
    protected readonly g: number;
    protected readonly b: number;
    protected readonly a: number;

    constructor(red: number, green: number, blue: number)
    constructor(red: number, green: number, blue: number, alpha: number = 1.0) {
        this.r = red;
        this.g = green;
        this.b = blue;
        this.a = alpha;
    }

    public get red(): number {
        return this.r;
    }

    public get green(): number {
        return this.g;
    }

    public get blue(): number {
        return this.b;
    }

    public get alpha(): number {
        return this.a;
    }
}

export const COLOR_BLACK = new Color(0.0, 0.0, 0.0);
export const COLOR_WHITE = new Color(1.0, 1.0, 1.0);
export const COLOR_RED = new Color(1.0, 0.0, 0.0);
export const COLOR_GREEN = new Color(0.0, 1.0, 0.0);
export const COLOR_BLUE = new Color(0.0, 0.0, 1.0);
