import {RenderEngine} from "./render/render.engine";
import {GameInterface} from "./game.interface";
import {Scene, SceneInterface} from "./game/objects/scene";

export class GameEngine implements GameInterface {
    private running: boolean;

    private frame_time: number = 0;
    private frame_count: number = 0;
    private frame_count_time: number = 0;

    private scene: Scene;

    constructor(public readonly renderEngine: RenderEngine) {
        this.running = false;
        this.scene = null;
    }

    public async start(targetFPS: number = 30): Promise<void> {
        if (this.running) {
            return;
        }

        if (this.hasScene()) {
            await this.scene.init();
        }

        this.running = true;
        console.info(`Game started with ${targetFPS} fps (${Math.round(1000 / targetFPS)} ms)`)
        this.process(targetFPS);
    }

    private process(targetFPS: number): boolean {
        if (!this.running) {
            console.warn('Game stopped');
            return false;
        }

        const start_time = new Date().getTime();

        if (this.hasScene) {
            this.scene.update();
        }

        this.renderEngine.clear();

        if (this.hasScene) {
            this.scene.render();
        }

        this.frame_count++;
        this.frame_time = new Date().getTime();
        this.frame_count_time += this.frame_time - start_time;

        if (this.frame_count >= targetFPS) {
            let spf = Math.round((this.frame_count / targetFPS) * 100) / 100;
            let fps = targetFPS;
            if (this.frame_count_time > 1000) {
                fps = Math.round((1000 / this.frame_count_time) * targetFPS);
            }
            console.info(`FPS ${fps} (${spf} ms)`);
            this.frame_count = 0;
            this.frame_count_time = 0;
        }

        const timeout = 1000 / targetFPS - this.frame_time + start_time;
        setTimeout(() => this.process(targetFPS), timeout < 1 ? 1 : timeout);
        return true;
    }

    public stop(): void {
        if (this.running) {
            this.running = false;
        }
    }

    public hasScene(): boolean {
        return this.scene !== null;
    }

    public async createScene(): Promise<SceneInterface> {
        this.scene = new Scene(this);
        return this.scene;
    }

    public toHTML(id: string = null): void {
        const el = document.createElement('div');
        el.id = 'manager';
        el.innerHTML = require('./game.pug')() + this.scene.toHTML();
        if (id !== null && document.getElementById(id)) {
            document.getElementById(id).appendChild(el);
        } else {
            document.body.appendChild(el);
        }
        console.info(`Game browser created`);
    }
}
