import {Texture} from "./texture";
import {TextureInterface} from "./texture.interface";

export class TextureEngine {
    private textures: { [name: string]: TextureInterface } = {};

    constructor(private readonly gl: WebGLRenderingContext) {
    }

    public loadTexture(url: string): TextureInterface {
        if (!this.textures.hasOwnProperty(url)) {
            this.textures[url] = new Texture(this.gl);
            const image = new Image();
            image.onload = () => this.textures[url].loadImage(image);
            image.src = url;
        }

        return this.textures[url];
    }
}
