import {ShaderInterface} from "./shader.interface";

class Shader {
    protected readonly shader: WebGLShader;

    constructor(protected readonly gl: WebGLRenderingContext, type: number, private readonly source: string) {
        this.gl = gl;

        const shader = gl.createShader(type);
        this.gl.shaderSource(shader, source);
        this.gl.compileShader(shader);

        if (!this.gl.getShaderParameter(shader, this.gl.COMPILE_STATUS)) {
            this.gl.deleteShader(shader);
            throw new Error(`An error occurred compiling the shaders: ${this.gl.getShaderInfoLog(shader)}`);
        }

        this.shader = shader;
    }

    public delete(): void {
        this.gl.deleteShader(this.shader);
    }

    public attach(program: WebGLProgram): void {
        this.gl.attachShader(program, this.shader);
    }

    public toHTML(type: string): string {
        return require('./shader.pug')({self: this, type});
    }
}

export class VertexShader extends Shader implements ShaderInterface {
    constructor(gl: WebGLRenderingContext, source: string) {
        super(gl, gl.VERTEX_SHADER, source);
    }

    public toHTML(): string {
        return super.toHTML('vertex');
    }
}

export class FragmentShader extends Shader implements ShaderInterface {
    constructor(gl: WebGLRenderingContext, source: string) {
        super(gl, gl.FRAGMENT_SHADER, source);
    }

    public toHTML(): string {
        return super.toHTML('fragment');
    }
}
