import {LayerInterface} from "./layer.interface";
import {GameObject, GameObjectType} from "../";
import {Camera, CameraInterface, OrthogonalCamera} from "../camera";
import {Entity, EntityInterface} from "../entity";
import {Scene, SceneInterface} from "../scene";
import {RenderInterface} from "../../../";

export class Layer extends GameObject implements LayerInterface {
    private camera: Camera;

    public constructor(renderer: RenderInterface, parent: Scene) {
        super(renderer, parent, GameObjectType.LAYER);
    }

    protected async onInit(): Promise<void> {
        await this.camera.init();
    }

    protected async onRender(): Promise<void> {
    }

    protected async onUpdate(): Promise<void> {
    }

    public createCamera(fn: (renderer: RenderInterface, parent: Layer) => Camera): Promise<CameraInterface> {
        this.camera = fn(this.renderer, this);
        return this.getCamera();
    }

    public createOrthogonalCamera(): Promise<CameraInterface> {
        return this.createCamera((renderer, parent) => {
            return new OrthogonalCamera(renderer, parent);
        });
    }

    public async getCamera(): Promise<CameraInterface> {
        return this.camera as CameraInterface;
    }

    public getScene(): Promise<SceneInterface> {
        return this.getParent<Scene>(GameObjectType.SCENE);
    }

    public addEntity(fn: (renderer: RenderInterface, parent: Layer) => Entity): Promise<EntityInterface> {
        return this.createObject<Entity>(fn(this.renderer, this));
    }

    public findEntity(name: string): Promise<EntityInterface> {
        return this.getObject<Entity>(name);
    }

    toHTML(html: string = ''): string {
        return super.toHTML(this.camera.toHTML());
    }
}
