import {EntityInterface} from "./entity.interface";
import {GameObject, GameObjectType} from "../";
import {Layer, LayerInterface} from "../layer";
import {Scene, SceneInterface} from "../scene";
import {CameraInterface} from "../camera";
import {RenderInterface} from "../../../render/render.interface";

export abstract class Entity extends GameObject implements EntityInterface {
    protected constructor(renderer: RenderInterface, parent: GameObject) {
        super(renderer, parent, GameObjectType.ENTITY);
    }

    protected async onInit(): Promise<void> {
    }

    protected async onRender(): Promise<void> {
    }

    protected async onUpdate(): Promise<void> {
    }

    public getLayer(): Promise<LayerInterface> {
        return this.getParent<Layer>(GameObjectType.LAYER);
    }

    public async getCamera(): Promise<CameraInterface> {
        return (await this.getLayer()).getCamera();
    }

    public getScene(): Promise<SceneInterface> {
        return this.getParent<Scene>(GameObjectType.SCENE);
    }
}
