import {FragmentShader, VertexShader} from "./shader";
import {ShaderProgram} from "./shader.program";
import {ShaderInterface} from "./shader.interface";

export class ShaderEngine {
    constructor(private readonly gl: WebGLRenderingContext) {
    }

    public initShaderProgram(vertexShader: ShaderInterface, fragmentShader: ShaderInterface): ShaderProgram {
        return new ShaderProgram(this.gl, vertexShader, fragmentShader);
    }

    public loadVertexShader(source: string): ShaderInterface {
        return new VertexShader(this.gl, source);
    }

    public loadFragmentShader(source: string): ShaderInterface {
        return new FragmentShader(this.gl, source);
    }
}
