import {Color, COLOR_BLACK} from "./color";
import {RenderInterface} from "./render.interface";
import {ShaderEngine} from "./shader";
import {TextureEngine} from "./texture";

export class RenderEngine implements RenderInterface {
    private readonly element: HTMLCanvasElement;
    private readonly gl: WebGLRenderingContext;

    public readonly shaderEngine: ShaderEngine;
    public readonly textureEngine: TextureEngine;

    private clearColor: Color = COLOR_BLACK;

    private static WIDTH: number = 800;
    private static HEIGHT: number = 600;

    constructor(id: string = null) {
        this.element = this.createCanvasElement(id);
        this.gl = this.createGlContext(this.element);
        this.shaderEngine = new ShaderEngine(this.gl);
        this.textureEngine = new TextureEngine(this.gl);
    }

    public get width(): number {
        return this.element.clientWidth;
    }

    public get height(): number {
        return this.element.clientHeight;
    }

    private createCanvasElement(id: string | null): HTMLCanvasElement {
        const el = document.createElement('canvas');
        el.id = 'game';
        if (id !== null && document.getElementById(id)) {
            document.getElementById(id).appendChild(el);
        } else {
            el.width = RenderEngine.WIDTH;
            el.height = RenderEngine.HEIGHT;
            document.body.appendChild(el);
        }
        console.info(`Canvas created (${el.clientWidth}x${el.clientHeight})`);
        return el;
    }

    private createGlContext(element: HTMLCanvasElement): WebGLRenderingContext {
        const gl = element.getContext("webgl2");
        if (gl === null) {
            throw new Error("Unable to initialize WebGL. Your browser or machine may not support it.");
        }
        return gl;
    }

    public clear(): void {
        this.gl.clearColor(this.clearColor.red, this.clearColor.green, this.clearColor.blue, this.clearColor.alpha);
        this.gl.clearDepth(1.0);
        this.gl.enable(this.gl.DEPTH_TEST);
        this.gl.depthFunc(this.gl.LEQUAL);
        this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);
    }

    private createBuffer(type: number, data: Float32Array | Uint16Array): WebGLBuffer {
        const buffer = this.gl.createBuffer();
        this.gl.bindBuffer(type, buffer);
        this.gl.bufferData(type, data, this.gl.STATIC_DRAW);
        return buffer;
    }

    public createArrayBuffer(data: number[]): WebGLBuffer {
        return this.createBuffer(this.gl.ARRAY_BUFFER, new Float32Array(data));
    }

    public createElementArrayBuffer(data: number[]): WebGLBuffer {
        return this.createBuffer(this.gl.ELEMENT_ARRAY_BUFFER, new Uint16Array(data));
    }

    private bindBuffer(type: number, buffer: WebGLBuffer): void {
        this.gl.bindBuffer(type, buffer);
    }

    public bindArrayBuffer(buffer: WebGLBuffer): void {
        return this.bindBuffer(this.gl.ARRAY_BUFFER, buffer);
    }

    public bindElementArrayBuffer(buffer: WebGLBuffer): void {
        return this.bindBuffer(this.gl.ELEMENT_ARRAY_BUFFER, buffer);
    }

    public drawArrays(offset: number, count: number) {
        this.gl.drawArrays(this.gl.TRIANGLE_STRIP, offset, count);
    }

    public drawTriangles(count: number, offset: number) {
        this.gl.drawElements(this.gl.TRIANGLES, count, this.gl.UNSIGNED_SHORT, offset);
    }
}
