import {v4} from 'uuid';

import {RenderInterface} from "../../render/render.interface";
import {GameObjectInterface} from "./game.object.interface";
import {GameObjectType} from "./game.object.type";

export abstract class GameObject implements GameObjectInterface {
    protected readonly id: string;

    private enabled: boolean = true;
    private hidden: boolean = false;
    private objects: { [name: string]: GameObject} = {};

    protected constructor(protected readonly renderer: RenderInterface, private readonly parent: GameObject = null, public readonly type: GameObjectType) {
        this.id = v4();
    }

    protected async createObject<T extends GameObject>(obj: GameObject): Promise<T> {
        this.objects[obj.id] = obj;
        return await this.getObject<T>(obj.id);
    }

    protected async getObject<T extends GameObject>(id: string): Promise<T> {
        if (this.objects.hasOwnProperty(id)) {
            return this.objects[id] as T;
        }
        throw new Error();
    }

    protected async getParent<T extends GameObject>(type: GameObjectType): Promise<T> {
        if (this.parent === null) {
            throw new Error();
        } else if (this.parent.type === type) {
            return this.parent as T;
        }
        return await this.parent.getParent<T>(type)
    }

    public get isEnabled(): boolean {
        return this.enabled;
    }

    public get isDisabled(): boolean {
        return !this.enabled;
    }

    public enable(): void {
        if (this.isEnabled) {
            return;
        }

        this.onEnable();
        this.enabled = true;

        console.info('Object enabled', this);
    }

    protected onEnable(): void {
    }

    public disable(): void {
        if (this.isDisabled) {
            return;
        }

        this.onDisable();
        this.enabled = false;

        console.info('Object disabled', this);
    }

    protected onDisable(): void {
    }

    public get isHidden(): boolean {
        return this.hidden;
    }

    public hide(): void {
        if (this.isHidden) {
            return;
        }

        this.onHide();
        this.hidden = true;
    }

    protected onHide(): void {
        //TODO: hide object
    }

    public show(): void {
        if (!this.isHidden) {
            return;
        }

        this.onShow();
        this.hidden = false;
    }

    protected onShow(): void {
        //TODO: object show
    }

    public async init(): Promise<void> {
        if (this.isDisabled) {
            return;
        }

        console.debug('Game Object - Init', this);

        await this.onInit();
        for (let key in this.objects) {
            await this.objects[key].init();
        }
    }

    protected abstract onInit(): Promise<void>;

    public render(): void {
        if (this.isDisabled || this.isHidden) {
            return;
        }

        this.onRender().then(() => {
            Object.values(this.objects).forEach(obj => obj.render());
        });
    }

    protected abstract onRender(): Promise<void>;

    public update(): void {
        if (this.isDisabled) {
            return;
        }

        this.onUpdate().then(() => {
            Object.values(this.objects).forEach(obj => obj.update());
        });
    }

    protected abstract onUpdate(): Promise<void>;

    public toHTML(html: string = '', self: any = {}): string {
        document.addEventListener(`${this.id}-display`, (e) => {
            document.querySelector(`[id='${this.id}']`).classList.toggle('hide');
        });
        document.addEventListener(`${this.id}-enable`, () => {
            if (this.isDisabled) {
                this.enable();
                document.querySelector(`[id='${this.id}'] button`).innerHTML = 'disable';
            } else {
                this.disable();
                document.querySelector(`[id='${this.id}'] button`).innerHTML = 'enable';
            }
        });
        return require('./game.object.pug')({self: {...this, ...self}, html});
    }
}
