import {mat4} from "gl-matrix";

import {RenderInterface} from "../../../render/render.interface";
import {GameObject} from "../game.object";
import {CameraInterface} from "./camera.interface";
import {GameObjectType} from "../game.object.type";
import {Layer, LayerInterface} from "../layer";

export abstract class Camera extends GameObject implements CameraInterface {
    public constructor(renderer: RenderInterface, parent: Layer) {
        super(renderer, parent, GameObjectType.CAMERA);
    }

    public abstract projectionMatrix(): mat4;

    public abstract modelViewMatrix(position: [number, number, number], scale: [number, number, number]): mat4;

    public async getLayer(): Promise<LayerInterface> {
        return await this.getParent<Layer>(GameObjectType.LAYER) as LayerInterface;
    }
}
