import {mat4} from "gl-matrix";

import {Camera} from "./camera";

export class OrthogonalCamera extends Camera {
    private readonly left: number = this.renderer.width / -2.0;
    private readonly right: number = this.renderer.width / 2.0;
    private readonly bottom: number = this.renderer.height / -2.0;
    private readonly top: number = this.renderer.height / 2;
    private readonly near: number = 0;
    private readonly far: number = 100;

    public projectionMatrix(): mat4 {
        const projectionMatrix = mat4.create();
        mat4.ortho(projectionMatrix, this.renderer.width / -2.0, this.renderer.width / 2.0, this.renderer.height / -2.0, this.renderer.height / 2, 0, 100);
        return projectionMatrix;
    }

    public modelViewMatrix(position: [number, number, number], scale: [number, number, number] = [1, 1, 1]): mat4 {
        const modelViewMatrix = mat4.create();
        mat4.translate(modelViewMatrix, modelViewMatrix, position);
        mat4.scale(modelViewMatrix, modelViewMatrix, scale);
        return modelViewMatrix;
    }

    protected onInit(): Promise<void> {
        return Promise.resolve(undefined);
    }

    protected onRender(): Promise<void> {
        return Promise.resolve(undefined);
    }

    protected onUpdate(): Promise<void> {
        return Promise.resolve(undefined);
    }

    public toHTML(html: string = ''): string {
        return super.toHTML(require('./orthogonal.camera.pug')({self: this}), {enabled: undefined});
    }
}
