import {mat4} from "gl-matrix";

import {ShaderInterface} from "./shader.interface";

export class ShaderProgram {
    protected readonly program: WebGLProgram;

    private attributes: { [name: string]: number} = {};
    private uniforms: { [name: string]: WebGLUniformLocation} = {};

    constructor(protected readonly gl: WebGLRenderingContext, private readonly vertexShader: ShaderInterface, private readonly fragmentShader: ShaderInterface) {
        const program = gl.createProgram();
        vertexShader.attach(program);
        fragmentShader.attach(program);
        this.gl.linkProgram(program);

        if (!this.gl.getProgramParameter(program, this.gl.LINK_STATUS)) {
            throw new Error(`Unable to initialize the shader program: ${this.gl.getProgramInfoLog(program)}`);
        }

        this.program = program;
    }

    public use(): void {
        this.gl.useProgram(this.program);
    }

    public getAttributeLocation(name: string): number {
        if (!this.attributes.hasOwnProperty(name)) {
            this.attributes[name] = this.gl.getAttribLocation(this.program, name);
        }
        return this.attributes[name];
    }

    public enableVertexAttributeArray(attribute: string): void {
        const location = this.getAttributeLocation(attribute);
        this.gl.enableVertexAttribArray(location);
    }

    public vertexAttributePointer(attribute: string, size: number): void {
        const location = this.getAttributeLocation(attribute);
        this.gl.vertexAttribPointer(location, size, this.gl.FLOAT, false, 0, 0);
    }

    public getUniformLocation(name: string): WebGLUniformLocation {
        if (!this.uniforms.hasOwnProperty(name)) {
            this.uniforms[name] = this.gl.getUniformLocation(this.program, name);
        }
        return this.uniforms[name];
    }

    public uniformMatrix4fv(uniform: string, data: mat4, transpose: boolean = false): void {
        const location = this.getUniformLocation(uniform);
        this.gl.uniformMatrix4fv(location, transpose, data);
    }

    public uniform1i(uniform: string, x: number = 0): void {
        const location = this.getUniformLocation(uniform);
        this.gl.uniform1i(location, x);
    }

    public toHTML(): string {
        return require('./shader.program.pug')({self: this});
    }
}
