import {TextureInterface} from "./texture.interface";

export class Texture implements TextureInterface {
    protected readonly texture: WebGLTexture;

    protected image: HTMLImageElement | null = null;

    constructor(gl: WebGLRenderingContext)
    constructor(protected readonly gl: WebGLRenderingContext, private readonly level: number = 0, private readonly internalFormat: number = null, private readonly srcFormat: number = null, private readonly srcType: number = null) {
        this.level = level;
        this.internalFormat = internalFormat ?? this.gl.RGBA;
        this.srcFormat = srcFormat ?? this.gl.RGBA;
        this.srcType = srcType ?? this.gl.UNSIGNED_BYTE;

        const texture = this.gl.createTexture();
        this.gl.bindTexture(this.gl.TEXTURE_2D, texture)
        this.gl.texImage2D(
            this.gl.TEXTURE_2D,
            this.level,
            this.internalFormat,
            1,
            1,
            0,
            this.srcFormat,
            this.srcType,
            new Uint8Array([0, 0, 255, 255])
        );

        this.texture = texture;

        this.gl.pixelStorei(gl.UNPACK_FLIP_Y_WEBGL, true);
    }

    public get width(): number {
        return this.image?.width ?? 1;
    }

    public get height(): number {
        return this.image?.width ?? 1;
    }

    public loadImage(image: HTMLImageElement): void {
        this.image = image;

        this.bind();
        this.gl.texImage2D(
            this.gl.TEXTURE_2D,
            this.level,
            this.internalFormat,
            this.srcFormat,
            this.srcType,
            this.image
        );

        this.gl.generateMipmap(this.gl.TEXTURE_2D);
    }

    public bind(): void {
        this.gl.activeTexture(this.gl.TEXTURE0);
        this.gl.bindTexture(this.gl.TEXTURE_2D, this.texture);
    }
}
