import {GameEngine, RenderEngine} from "./engine";
import {Sprite} from "./engine/game/objects/sprite/sprite";

main().then();

async function main() {
    const renderEngine = new RenderEngine();
    const gameEngine = new GameEngine(renderEngine);

    const scene = await gameEngine.createScene();
    const layer = await scene.createLayer();
    await layer.createOrthogonalCamera();
    await layer.addEntity((renderer, parent) => {
        const sprite = new Sprite(renderer, parent, 'df.png');
        sprite.scale = [64, 64, 0];
        return sprite;
    });

    gameEngine.start().then(() => {
        gameEngine.toHTML();
    });
}
