import {GameEngine} from "../../../game.engine";
import {GameObject} from "../game.object";
import {SceneInterface} from "./scene.interface";
import {GameObjectType} from "../game.object.type";
import {Layer, LayerInterface} from "../layer";

export class Scene extends GameObject implements SceneInterface {
    constructor(private readonly engine: GameEngine) {
        super(engine.renderEngine, null, GameObjectType.SCENE);

        this.engine = engine;
    }

    protected async onInit(): Promise<void> {
    }

    protected async onRender(): Promise<void> {
    }

    protected async onUpdate(): Promise<void> {
    }

    public createLayer(): Promise<LayerInterface> {
        return this.createObject<Layer>(new Layer(this.renderer, this));
    }

    public getLayer(id: string): Promise<LayerInterface> {
        return this.getObject<Layer>(id);
    }

    public toHTML(): string {
        return require('./scene.pug')() + super.toHTML();
    }
}
